<template>
    <h-baseView :loading="loading" isScroll>
        <el-button slot="baseViewNav" plain type="primary" @click="$refs.FormAE.start(false)">新增</el-button>
        <div class="document">
            <div class="left">
                <el-tree :data="treeData" node-key="Id" :props="defaultProps" default-expand-all :expand-on-click-node="false" highlight-current empty-text="">
                    <span class="custom-tree-node" slot-scope="{ node,data }">
                        <p class="class-name" @click="clickItem(data.Id)">{{ node.label }} </p>
                    </span>
                </el-tree>
            </div>
            <div class="right">
                <el-table :data="tableData" highlight-current-row stripe border height="calc(100% - 40px)" v-loading="loading" :empty-text="' '" @selection-change="sChange">
                    <div slot="empty" class="iconfont icon-empty" v-if="!loading" />
                    <el-table-column type="selection" width="50" align="center" />
                    <el-table-column fixed type="index" label="序号" width="50" align="center" />
                    <el-table-column prop="FName" label="标题" min-width="100" align="left" header-align="center">
                        <template slot-scope="scope">
                            <span class="yclick hand" @click="$refs.FormDetails.start(scope.row)">{{scope.row.FName}}</span>
                        </template>
                    </el-table-column>
                    <el-table-column prop="CatalogName" label="目录" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateName" label="发布人" width="100" align="center"></el-table-column>
                    <el-table-column prop="CreateTime" label="创建时间" width="160" align="center"></el-table-column>

                    <el-table-column label="操作" width="120" align="center" fixed="right">
                        <template slot-scope="scope">
                            <i class="iconfont icon-confirm-audit" title="审核" v-show="scope.row.FStatus == 1" @click="audit(apis.audit, 3,scope.row)"></i>
                            <i class="iconfont icon-cancel-audit" title="反审核" v-show="scope.row.FStatus == 3" @click="audit(apis.audit, 1,scope.row)"></i>
                            <i class="iconfont icon-edit" title="编辑" v-show="scope.row.FStatus == 1" @click="$refs.FormAE.start(scope.row)"></i>
                            <i class="iconfont icon-remove" title="删除" v-show="scope.row.FStatus == 1" @click="remove(apis.delete,scope.row.Id,getList)"></i>
                        </template>
                    </el-table-column>
                </el-table>
                <page ref="page" @refreshList="getList" />
            </div>
        </div>
        <FormAE ref="FormAE" :apis="apis" @refresh="getList" FormsCode="oa_document" upLoadBeforSubmit imgNameField="FIcon" />
        <FormDetails ref="FormDetails" :apis="apis" FormsCode="oa_document" />
        <DocumentDetails ref="FormDetails" :apis="apis" />

    </h-baseView>
</template>

<script>
export default {
    name: "CardList",
    props: {},
    components: {
        FormAE: () => import("@/universalPage/formAE/FormAE.vue"),
        FormDetails: () => import("@/universalPage/formDetails/FormDetails.vue"),
        DocumentDetails: () => import("./DocumentDetails.vue")
    },
    data() {
        return {
            // 页面加载
            loading: false,
            tableData: [],
            treeData: [],
            defaultProps: {
                children: "children",
                label: "CatalogName"
            },
            apis: {
                edit: this._api.OA_DC_DocumentsEdit,
                create: this._api.OA_DC_DocumentsCreate,
                list: this._api.OA_DC_DocumentsManage,
                delete: this._api.OA_DC_DocumentsDelete,
                audit: this._api.OA_DC_DocumentsAudit,
                details: this._api.OA_DC_DocumentsDetails
            },
            searchInfo: {}
        };
    },
    methods: {
        getList(reSetPage) {
            let Page = this.$refs.page
            if (reSetPage) Page.Page = 1
            this.loading = true
            this.apis.list && this.apis.list({
                page: Page.Page,
                pageSize: Page.PageSize,
                ...this.searchInfo
            }).then(res => {
                if (res) {
                    this.tableData = res.data
                    Page.totalcount = res.totalcount
                }
                this.loading = false
            })
        },
        OA_DC_CatalogIndex() {
            this._api.OA_DC_CatalogIndex().then(res => {
                if (res) {
                    this.treeData = this.$u.arraytoTree(res.data)
                }
            })
        },
        clickItem(Id) {
            console.log(Id)
            this.searchInfo.CatalogId = Id
            this.getList(1)
        }
    },

    mounted() {
        this.OA_DC_CatalogIndex()
        this.getList()
    }
};
</script>

<style lang="scss" scoped>
.document {
    display: flex;
    width: 100%;
    height: 100%;

    .left {
        width: 360px;
        height: 100%;
        border-right: 1px solid #eceef2;
    }

    .right {
        width: calc(100% - 360px);
        height: 100%;
        padding-left: 15px;
    }
}
</style>